a{
    text-decoration: none !important;
 }

 .btn-body{
    height:80px;
    width:80px;
    border-radius:20px;
    color:#F8D9C0;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin:5px auto;
    transition: all 0.2s ease;
    
}

.btn-body.active{
    background-color: rgba(244,187,146,1);
    color:#fff;
    transform: scale(1.05);
}

.btn-body:hover{
    color:#fff;
}

 .btn-title{
    margin: 4px auto;
    font-weight:600;
    font-size: 14px;
 }